<template>
  <div class="culture pt60">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 bg-fff">
        <currentTab :productList="productList"></currentTab>
        <div class="public_content">
            <barTitle :title="title"></barTitle>
            <div class="mt40">
                <ul>
                    <li v-for="(item,i) in cultureList" :key="i" class="pb80 dfrcb">
                        <div class="culture_img">
                            <img :src="item.img" alt="">
                        </div>
                        <div class="culture_info">
                            <div class="fs28">{{item.top}}</div>
                            <div class="fs20 mt10">{{item.middle}}</div>
                            <div class="fs14 mt50" :class="{'mt80': item.id == '2'}">{{item.bottom}}</div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "关于我们",
        title: "About Us",
        companyInfo:
          "湖南思利他网络科技有限公司于2017年09月01日成立。注册资本2017余万人民币、思利他的创始人、 股东及投资人，均是从事建筑相关行业，在招投标、项目管理上有着非常丰富的经验。"
      },
      title: "企业文化",
      productList: [
        {
          id: 0,
          title: "公司简介",
          path: "/companyInfo"
        },
        {
          id: 1,
          title: "发展历程",
          path: "/history"
        },
        {
          id: 2,
          title: "企业文化",
          path: "/culture"
        }
      ],
      cultureList: [
          {
              id: "1",
              img: require("../assets/src/images/img5.png"),
              top: "公司使命：打造建筑业招投标第一服务平台",
              middle: "Mission: to build the first service platform for bidding in construction industry",
              bottom: "我们追求的是为建筑业领域打造一个数据服务平台，并依靠丰富的建筑业经验和互联网思维，全心全意为用户提供优质、有价值的产品。为此我们全体员工愿付出所有的努力，都将围绕着我们的使命和社会主义核心价值观，传承我们的追求，牢记并努力实现我们的企业价值。"
          },
          {
             id: "2",
             img: require("../assets/src/images/img6.png"),
             top: "公司愿景：积善行、思利他",
             middle: "Vision of the company: good deeds and altruism",
             bottom: "“在真心为他人努力付出后，收获我们的自身的价值”。"
          }
      ]
    };
  }
};
</script>
<style scoped lang="less">
@import "../styles/global";
.culture /deep/ .top_bar {
  background-image: url("../assets/src/images/banner-info2.png");
}
.culture {
    .culture_img {
        width: 450px;
        height: 500px;
        img {
            width: 100%;
            height: 100%;
        }
    }
    .culture_info {
        width: 650px;
    }
}
</style>


