<template>
  <div class="history pt60">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 pb30 bg-fff">
      <currentTab :productList="productList"></currentTab>
      <div class="public_content">
        <barTitle :title="title"></barTitle>
        <div style="height: 2000px;">
          <ul class="contract-operate_recode_date">
            <li v-for="(item,i) in historyList" :key="i" class="dfr">
              <div class="operate-date" :class="{'showColor': item.id == '6'}">{{item.time}}</div>
              <i></i>
              <div class="operate-des" :class="{'showColor': item.id == '6'}">{{item.product}}</div>
            </li>
            <!-- <li>
              <span>2019-01-05 13:22:32</span>
              <i></i>
              <span>文件1我方签章 操作人：李莲</span>
            </li>
            <li>
              <span>2019-01-08 16:02:02</span>
              <i></i>
              <span>文件1对方签章 操作人：王二</span>
            </li>
            <li>
              <span>2019-01-10 10:11:21</span>
              <i></i>
              <span>文件1合同签署完成</span>
            </li> -->
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "关于我们",
        title: "About Us",
        companyInfo:
          "湖南思利他网络科技有限公司于2017年09月01日成立。注册资本2017余万人民币、思利他的创始人、 股东及投资人，均是从事建筑相关行业，在招投标、项目管理上有着非常丰富的经验。"
      },
      title: "发展历程",
      productList: [
        {
          id: 0,
          title: "公司简介",
          path: "/companyInfo"
        },
        {
          id: 1,
          title: "发展历程",
          path: "/history"
        },
        {
          id: 2,
          title: "企业文化",
          path: "/culture"
        }
      ],
      historyList: [
          {   
              id: "0",
              time: "截至2019年10月20日",
              product: "标大大注册用户将近2000、日活跃用户将近1000、用户几户覆盖了整个 湖南地区。"
          },
          {
              id: "1",
              time: "2019年4月25日",
              product: "标大大会员制正式上线"
          },
          {
              id: "2",
              time: "2019年3月28日",
              product: "综合查询正式上线"
          },
          {
              id: "3",
              time: "2018年12月",
              product: "正式接外包项目：日精进打卡小程序"
          },
          {
              id: "4",
              time: "2018年10月份",
              product: "软件著作权审核通过"
          },
          {
              id: "5",
              time: "2017年10月16日",
              product: "标大大app正式上线"
          },
          {
              id: "6",
              time: "2017年9月1日",
              product: "湖南思利他网络科技有限公司正式成立"
          },
          {
              id: "7",
              time: "2017年4月14日",
              product: "“招投标秘书“改名为标大大”"
          },
          {
              id: "8",
              time: "2016年8月份",
              product: "拜访阿里巴巴、启信宝、天眼查对接工商、诚信数据"
          },
          {
              id: "9",
              time: "2015年9月份",
              product: "“招投标秘书”网页端正式上线"
          },
          {
              id: "10",
              time: "2014年9月份",
              product: "正式组建团队开发“招投标秘书”"
          },
      ]
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.history /deep/ .top_bar {
  background-image: url("../assets/src/images/banner-info2.png");
}
.contract-operate_recode_date {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  li {
    width: 100%;
    :nth-child(1) {
      width: 119px;
      height: 30px;
      line-height: 30px;
      font-size: 20px;
      display: inline-block;
      width: 200px;
      text-align: right;
    }
    :nth-child(2) {
        &:after {
        content: "";
        display: block;
        clear: both;
        width: 1px;
        height: 200px;
        background-color: @mainColor;
        margin: 8px auto;
      }
      display: inline-block;
      background-color: @mainColor !important;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      margin: 0 20px;
    }
    :nth-child(3) {
      font-size: 12px;
      display: inline-block;
      height: 16px;
      font-size: 28px;
      width: 885px;
      height: 80px;
    }
  }
  li:last-child {
    :nth-child(1) {
      font-size: 12px;
      display: inline-block;
      width: 119px;
      height: 30px;
      line-height: 30px;
      font-size: 20px;
      display: inline-block;
      width: 200px;
      text-align: right;
    }
    :nth-child(2) {
      display: inline-block;
      background-color: @mainColor !important;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      &:after {
        content: "";
        display: none;
      }
    }

    :nth-child(3) {
      font-size: 12px;
      letter-spacing: 0;
      display: inline-block;
      height: 16px;
      font-size: 28px;
      width: 885px;
      height: 80px;
    }
  }
  .showColor {
      color: @mainColor;
  }
}
</style>


