        <div>
          <div class="fs28 mt40 mb20">湖南思利他网络科技有限公司</div>
          <div
            class="fs18 mb40"
          >湖南思利他网络科技有限公司于2017年09月01日成立。注册资本2017余万人民币、思利他的创始人、股东及投资人，均是从事建筑相关行业，在招投标、项目管理上有着非常丰富的经验。在互联网掀起了一波浪潮，各行业都通过互联网得到了飞速的发展背景下成立湖南思利他网络科技有限公司。最终我们希望能通过互联网+建筑的手段，为建筑行业发展做一些力所能及的事。</div>
          <ul class="dfrb mb40">
