                <img :src="item.img" alt />
              </div>
              <div class="fs28 mt40 mb20">{{item.info}}</div>
              <ul>
                <li v-for="(a,b) in item.infoList" :key="b">
                  <div class="fs14">{{a.info}}</div>
                </li>
              </ul>
            </li>
            <li class="info_item_li">
                <div class="info_img">
                    <img :src="img2" alt="">
                </div>
                <div class="fs28 mt40 mb20">使命</div>
                <div class="fs14">积善之家有余庆。在这个世界上，存在着因果报应的法则，如果多做好事、善事，那么家人、家族就会有好报，这种好报还会贯穿你的一生。利他的行为，就是以亲切、同情、和善、慈悲之心去待人接物，这种行为，一定会给你带来莫大的幸运。多做好事，就能使命运朝着好的方向转变，使自己的工作朝着好的方向转变。为他人、为社会竭尽全力，这是使人生、也是使经营朝着更好方向转变的唯一的方法。</div>
            </li>
            <li class="info_item_li">
                <div class="info_img">
                    <img :src="img1" alt="">
                </div>
                <div class="fs28 mt40 mb20">核心价值观</div>
                <div class="mb20 fs14">
                    <div>付出不亚于任何人的努力</div>
                    <div>Give as much as anyone</div>
                </div>
                <div class="mb20 fs14">
                    <div>要谦虚，不要骄傲</div>
                    <div>Be modest, not proud</div>
                </div>
                <div class="mb20 fs14">
                    <div>积善行，思利他</div>
                    <div>Good deeds and altruism</div>
                </div>
                <div class="mb20 fs14">
                    <div>不要有感性的烦恼</div>
                    <div>Don't have emotional worries</div>
                </div>
