    return {
      titleInfo: {
        themem: "关于我们",
        title: "About Us",
        companyInfo:
          "湖南思利他网络科技有限公司于2017年09月01日成立。注册资本2017余万人民币、思利他的创始人、 股东及投资人，均是从事建筑相关行业，在招投标、项目管理上有着非常丰富的经验。"
      },
      title: "公司简介",
      productList: [
        {
          id: 0,
          title: "公司简介",
          path: "/companyInfo"
        },
        {
          id: 1,
          title: "发展历程",
          path: "/history"
        },
        {
          id: 2,
          title: "企业文化",
          path: "/culture"
        }
      ],
      companyInfoList: [
        {
          img: require("../assets/src/images/img1.png"),
          info: "关于“思利他”名字的由来",
          infoList: [
            {
