            }
          ]
        },
        {
          img: require("../assets/src/images/img2.png"),
          info: "愿景",
          infoList: [
            {
              info: "积善行、思利他"
            },
            {
              info: "在真心为他人努力付出后，收获我们的自身的价值"
            }
          ]
        },
      ],
      img1: require("../assets/src/images/img4.png"),
      img2: require("../assets/src/images/img3.png")
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.companyInfo /deep/ .top_bar {
  background-image: url("../assets/src/images/banner-info2.png");
}
.companyInfo {
  .info_item {
    width: 580px;
    height: 540px;
    .info_img {
      width: 580px;
      height: 360px;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .info_item_li {
      width: 580px;
  }
}
</style>


